<template>
  <div class="main_right">
    <div class="w1000 mt100 pl112">
      <div class="logic_cot">
        <div class="global_logic_tpl">
          <div class="global_logic_set">
            <div class="poplayer tccCon_t global_content">
              <div class="logic_title">
                跳转逻辑
              </div>
              <div class="logicsset jump_content">
                  <div class="jump_header">
                      <span>共</span>
                      <span class="tz_num">{{rules.length}}. </span>
                      <span>条跳转逻辑</span>
                      <a class="btn-add btn-add-jump" @click="addRule">
                          <i></i>新增逻辑
                      </a>
                  </div>
                  <div id="global_jump" class="global_jump">
                      <table>
                          <tbody>
                              <tr class="jump_row jump_row_edit" v-for="(rule, ruleIndex) in rules" :key="ruleIndex">
                                  <td class="add_num">1. </td>
                                  <td>如果&nbsp;&nbsp;</td>
                                  <td>
                                      <!-- <select name="source_q">
                                          <option value="">请选择题目</option>
                                      </select> -->
                                      <el-select v-model="rule.qid" placeholder="请选择题目" size="small" @change="selectQuestion">
                                        <el-option
                                          v-for="(item, index) in questions"
                                          :key="item.qid"
                                          :label="'Q' + index + ':' + item.subject"
                                          :value="item.qid">
                                        </el-option>
                                      </el-select>
                                  </td>
                                  <td style="text-align:center;" class="td_txt">&nbsp;&nbsp;选择了&nbsp;&nbsp;</td>
                                  <td>
                                      <!-- <select name="option_id" value="" disabled="disabled">
                                          <option value="" selected="">请选择选项</option>
                                      </select> -->
                                      <el-select v-model="rule.qoid" placeholder="请选择选项" size="small">
                                        <el-option
                                          v-for="item in questions[selectedQuestion].options"
                                          :key="item.oid"
                                          :label="item.content"
                                          :value="item.oid">
                                        </el-option>
                                      </el-select>
                                      <em>&nbsp;&nbsp;则跳过&nbsp;&nbsp;</em>
                                      <!-- <select name="jump_to_qid" disabled="disabled" value="">
                                          <option value="">请选择题目</option>
                                      </select> -->
                                      <el-select @change="saveLogic(ruleIndex)" v-model="rule.target_qid_arr" placeholder="请选择题目" size="small" multiple collapse-tags>
                                        <el-option
                                          v-for="(item, index) in questions"
                                          :key="item.qid"
                                          :label="'Q' + index + ':' + item.subject"
                                          :value="item.qid">
                                        </el-option>
                                      </el-select>
                                      <div class="reduce" @click="deleteLogic(ruleIndex)"></div>
                                  </td>
                              </tr>
                          </tbody>
                      </table>
                  </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '~/api/api';
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';
  import {mapState} from 'vuex';
  import axios from 'axios';

  export default {
    data() {
      return {
        rules: [],
        questions: null,
        selectedQuestion: 0,
      };
    },
    computed: {
      ...mapState({
        selectedItem: state => state.selectedItem,
      })
    },
    mounted() {
      this.init();
    },
    methods: {
      selectQuestion(val){
        this.selectedQuestion = this.questions.findIndex(x => x.qid == val);
      },
      addRule() {
        this.rules.push({
          sid: this.$route.params.sid,
          qid: '',
          qoid: '',
          target_qid: '',
          target_qid_arr: []
        })
      },
      saveLogic(ruleIndex) {
        this.rules[ruleIndex].target_qid = this.rules[ruleIndex].target_qid_arr.join(',');
        api.saveRule(this.rules[ruleIndex]).then(res=>{
          if(!res) return;
          this.rules[ruleIndex].id = res.data.id;
        })
      },
      deleteLogic(ruleIndex) {
        api.delelteLogic(this.rules[ruleIndex].id).then(res => {
          if (res) {
            this.rules.splice(ruleIndex, 1);
          }
        })
      },
      init() {
        let sid = this.$route.params.sid;
        if(!sid) return;
        let _this = this;
        axios.all([api.getInfo(sid), api.getRuleList(sid)]).then(res => {
          _this.questions = res[0].question.filter(x => /^[1|2|4|5]/.test(x.qtype));
          res[1].data.forEach(item => {
            item.target_qid_arr = item.target_qid.split(',');
            _this.rules.push(item);
          });
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
    .pt64 {
        padding-top: 64px;
        box-sizing: border-box;
    }
    .main_right {
        overflow: auto;
        height: 100%;
    }
    .pl112 {
        padding-left: 112px;
    }
    .mt100 {
        margin-bottom: 100px;
    }
    .w1000 {
        width: 1000px;
        margin: 0 auto;
        padding: 0 40px;
    }
    .logic_cot{
        margin-bottom:100px;
        .global_logic_set {
          .tccCon_t{
            padding:0;
          }
          .logic_title {
            font-size: 18px;
            height: 45px;
            line-height: 45px;
            color: #484848;
            text-indent: 20px;
            padding-top: 20px;
            margin-bottom 40px
          }
          .poplayer {
            .jump_content{
              background-color: #fff;
              border-radius: 2px;
              box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
              position: relative;
              .global_jump {
                table {
                  width: 100%;
                  tr {
                    height: 50px;
                    line-height: 50px;
                    &.jump_row {
                      border-bottom: 1px solid #eee;
                    }
                    td {
                      &:nth-child(1){
                        padding-left:40px;
                        width: 24px;
                        font-size: 14px;
                        color: #484848;
                      }
                      &:nth-child(2){
                        font-size: 14px;
                        color: #484848;
                      }
                      &:nth-child(3){
                        width: 160px;
                      }
                      &:nth-child(4){
                        font-size: 14px;
                        color: #484848;
                        height: 32px;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .jump_header{
          padding: 0px 40px;
          border-bottom:1px solid #eee;
          height: 64px;
          line-height: 64px;
          position: relative;
          span{
            font-size: 14px;
            color:#aaa;
          }
        }

        em {
          font-style: normal;
        }

        .btn-add {
            width: 94px;
            height: 30px;
            border-radius: 2px;
            background-color: #2672ff;
            color: #ffffff;
            font-size: 12px;
            line-height: 30px;
            cursor: pointer;
            margin-top: 16px;
            position: absolute;
            top: 0px;
            right: 40px;
            i {
              display: inline-block;
              background: url(../../../images/icons.png) no-repeat;
              background-position: -192px 0px;
              background-size: 520px auto;
              width: 24px;
              height: 24px;
              vertical-align: middle;
              margin: -2px 6px 0 6px;
            }
        }

        .reduce {
            display: inline-block;
            background: url(../../../images/icons.png) no-repeat;
            background-position: -144px -24px;
            background-size: 520px auto;
            width: 24px;
            height: 24px;
            vertical-align: middle;
            margin: 14px 40px 0 0;
            cursor: pointer;
            float: right;
        }
    }
</style>


